import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { Typography, TextField, withStyles, Popover, Fade, Zoom, Button } from '@material-ui/core';
import { Map, EventSeat, Done } from '@material-ui/icons';

const styles = theme => {
    return {
        highlight: {
            color: theme.palette.primary.main
        },
        textField: {
            width: '75%',
            marginTop: theme.spacing.unit,
        },
        popoverTypography: {
            padding: theme.spacing.unit * 2
        },
        superpose: {
            position: 'absolute',
            width: `calc(100% - ${theme.spacing.unit * 4}px)`
        },
        sendToBack: {
            zIndex: -1
        },
        buttonOk: {
            marginTop: '20px',
        },
        questionTextContainer: {
            display: 'inline-flex',
            justifyContent: 'center'
        },
        questionText: {
            width: '75%'
        }
    }
}

class Question extends Component {
    constructor(props) {
        super(props)

        this.state = {
            answerValue: '',
            anchorElement: null,
            answerAnimation: true,
            okAnimation: false
        };
        this.animationRunning = false;
    }

    handleChange = (e) => {
        const answer = e.target.value;
        this.setState({ answerValue: answer });
        this.checkQuestion(answer);
    }

    checkQuestion = (answer) => {
        if (this.props.question.answer.includes(answer.toLowerCase())) {
            this.animationRunning = true;
            this.animateAnswer(false);
        }
    }

    handleAnswerExiting = () => {
        this.animateOk(true);
    }

    handleOkEntered = () => {
        setTimeout(() => this.animateOk(false), 500);
    }

    handleOkExited = () => {
        if (this.animationRunning === true) {
            this.props.onCorrectAnswer();
            this.animationRunning = false;
        }
        this.setState({ answerValue: '' });
        this.animateAnswer(true);
    }

    handleAnswerEntered = () => {
        // if (this.animationRunning === true) {
        //     this.props.onCorrectAnswer();
        //     this.animationRunning = false;
        // }
    }

    animateAnswer = (value) => {
        this.setState({ answerAnimation: value });
    }

    animateOk = (value) => {
        this.setState({ okAnimation: value });
    }

    handleIconClick = (event) => {
        this.setState({ anchorElement: event.target });
    }

    handleClose = () => {
        this.setState({ anchorElement: null });
    }

    render() {
        const question = this.props.question;

        if (!question)
            return (
                <Fade in={!question}>
                    <div>
                        <Typography variant='h4' paragraph color='primary'>
                            ¬°Felicitaciones!
                        </Typography>
                        <Typography variant='h6' paragraph>
                            ¬°Completaste la b√∫squeda!                            
                        </Typography>
                        <Typography variant='body1' paragraph>
                            Toc√° la llave de arriba a la izquierda para abrir el cofre y encontrar el premio
                        </Typography>
                    </div>
                </Fade>
            );

        const { classes } = this.props;
        return (
            <div>
                <div className={classes.superpose}>
                    <Fade in={this.state.answerAnimation}>
                        <div>
                            <Typography variant='h6' className={classes.highlight}>
                                {question.hasMap && <Map className={classes.icon} onClick={this.handleIconClick} />}
                                {!question.hasMap && <EventSeat className={classes.icon} onClick={this.handleIconClick} />}
                            </Typography>
                            <div className={classes.questionTextContainer}>
                                <Typography variant='h6' className={classes.questionText}>
                                    {question.question}
                                </Typography>
                            </div>
                            <Popover
                                id="icon-popover"
                                open={Boolean(this.state.anchorElement)}
                                anchorEl={this.state.anchorElement}
                                onClose={this.handleClose}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'center',
                                }}
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'center',
                                }}>
                                <Typography className={classes.popoverTypography}>
                                    {question.hasMap && <>La respuesta est√° en el lugar que dice la pista</>}
                                    {!question.hasMap && <>La respuesta la pod√©s poner sin moverte de donde est√°s</>}
                                </Typography>
                            </Popover>
                        </div>
                    </Fade>
                    <div>
                        <Fade
                            in={this.state.answerAnimation}
                            onEntered={this.handleAnswerEntered}
                            onExited={this.handleAnswerExiting}>
                            <TextField
                                className={classes.textField}
                                onChange={this.handleChange}
                                value={this.state.answerValue}
                                label='Respuesta' />
                        </Fade>
                    </div>
                </div>
                <div className={`${classes.superpose} ${classes.sendToBack}`}>
                    <Zoom
                        in={this.state.okAnimation}
                        onEntered={this.handleOkEntered}
                        onExited={this.handleOkExited}>
                        <Button
                            className={classes.buttonOk}
                            variant='fab'
                            color='primary'>
                            <Done color='white' />
                        </Button>
                    </Zoom>
                </div>
            </div>
        )
    }
}

Question.propTypes = {
    question: PropTypes.object,
    onCorrectAnswer: PropTypes.func.isRequired
}

export default withStyles(styles)(Question);
