import React, { Component } from 'react'
import VpnKey from '@material-ui/icons/VpnKey'
import { Button, withStyles } from '@material-ui/core';
import PropTypes from 'prop-types'

const styles = theme => {
    return {
        text: {
            marginLeft: theme.spacing.unit
        }
    };
}

class KeyCounter extends Component {
    render() {
        const { classes } = this.props;
        return (
            <div className={this.props.className}>
                <Button onClick={this.props.onClick}>
                    <VpnKey />
                    <span className={classes.text}>
                        {this.props.keys} / {this.props.totalKeys}
                    </span>
                </Button>
            </div>
        )
    }
}

KeyCounter.propTypes = {
    keys: PropTypes.number.isRequired,
    totalKeys: PropTypes.number.isRequired,
    onClick: PropTypes.func.isRequired
}

export default withStyles(styles)(KeyCounter);