import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import PropTypes from 'prop-types'
import questionList from '../questionList';
import Question from './Question';
import KeyCounter from './KeyCounter';
import { withStyles, Slide } from '@material-ui/core';
import Chest from './Chest'

const styles = theme => {
    return {
        keyCounter: {
            textAlign: 'left',
            marginBottom: theme.spacing.unit * 2
        },
        slideContainer: {
            left: '0px',
            top: '0px',
            position: 'absolute',
            padding: '16px',
            width: '100%',
            backgroundColor: theme.palette.background.default
        }
    }
}

class QuestionsContainer extends Component {
    constructor(props) {
        super(props);

        let questionNumber = parseInt(localStorage.getItem('questionNumber'));
        if (!questionNumber) {
            questionNumber = 0;
            localStorage.setItem('questionNumber', questionNumber);
        }

        this.state = {
            questionNumber: questionNumber
        };
    }

    handleCorrectAnswer = () => {
        let newQuestionNumber = parseInt(this.state.questionNumber) + 1;

        if (newQuestionNumber === questionList.length) {
            //this.setState({ showChest: true });
        }

        localStorage.setItem('questionNumber', newQuestionNumber);
        this.setState({ questionNumber: newQuestionNumber });
    }

    render() {
        return (
            <div>
                <KeyCounter
                    className={this.props.classes.keyCounter}
                    keys={this.state.questionNumber}
                    totalKeys={questionList.length}
                    onClick={() => this.setState({ showChest: true })} />
                <Question
                    question={questionList[this.state.questionNumber]}
                    onCorrectAnswer={this.handleCorrectAnswer} />

                <Slide direction='up' in={this.state.showChest} mountOnEnter unmountOnExit>
                    <div className={this.props.classes.slideContainer}>
                        <Chest
                            keys={this.state.questionNumber}
                            totalKeys={questionList.length}
                            onClose={() => this.setState({ showChest: false })} />
                    </div>
                </Slide>
            </div>
        )
    }
}

QuestionsContainer.propTypes = {
    onCorrectAnswer: PropTypes.func
}

export default withStyles(styles)(withRouter(QuestionsContainer));
