import React, { Component } from 'react'
import { LinearProgress, withStyles } from '@material-ui/core';
import { ArrowBack } from '@material-ui/icons';
import './chestAnimation.css'
import KeyCounter from './KeyCounter';
import { blueGrey } from '@material-ui/core/colors';

const styles = theme => {
    return {
        container: {
            zIndex: 2
        },
        arrowBack: {
            textAlign: 'left',
            marginBottom: theme.spacing.unit * 2,
            cursor: 'pointer',
            color: 'white',
            marginTop: '5px'
        },
        chest: {
            width: 'auto',
            maxHeight: '300px',
        },
        progressContainer: {
            width: '75%',
            margin: `${theme.spacing.unit * 3}px auto ${theme.spacing.unit}px auto`
        },
        progress: {
            backgroundColor: blueGrey[700],
        },
        keys: {
            display: 'inline-flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        keyIcon: {
            color: 'white',
            height: '20px'
        },
        text: {
            width: '75%',
            textAlign: 'center',
            margin: `${theme.spacing.unit * 2}px auto 0px`
        }
    }
}

class Chest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            progress: 0,
            chestAnimation: '',
            chestZoomOut: false
        };
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ progress: this.props.keys * 100 / this.props.totalKeys });
        }, 100);
    }

    onChestClick = () => {
        if (this.props.keys === this.props.totalKeys) {
            if /* if we're on iOS, open in Apple Maps */
                ((navigator.platform.indexOf("iPhone") !== -1) ||
                (navigator.platform.indexOf("iPad") !== -1) ||
                (navigator.platform.indexOf("iPod") !== -1))
                window.open("maps://maps.google.com/maps?daddr=-54,-38&amp;ll=");

            else /* else use Google */
                window.open("https://maps.google.com/maps?daddr=-54,-38&amp;ll=");

        }
        else {
            this.setState({ chestAnimation: 'chestAnimation' });
        }
    }

    onChestAnimationEnd = () => {
        this.setState({ chestAnimation: '' });
    }

    render() {
        let { classes } = this.props;
        return (
            <div className={classes.container}>
                <div className={classes.arrowBack}>
                    <ArrowBack onClick={this.props.onClose} />
                </div>
                <img
                    src='chest.jpg' alt='Cofre del tesoro'
                    className={`${classes.chest} ${this.state.chestAnimation} ${this.state.chestOpenAnimation}`}
                    onClick={this.onChestClick}
                    onAnimationEnd={this.onChestAnimationEnd} />

                {/* <Typography variant='body1' paragraph className={classes.text}>
                    Este el es cofre del tesoro<br />Para abrirlo necesit√°s {this.props.totalKeys} llaves&nbsp;
                    {this.props.keys === this.props.totalKeys && <>¬°y ya las ten√©s todas! ¬°Toc√° el cofre para abrirlo!</>}
                    {(this.props.keys < this.props.totalKeys && this.props.keys !== 0) && <>pero solo ten√©s {this.props.keys}<br />Segu√≠ contestando preguntas para avanzar :)</>}
                    {this.props.keys === 0 && <>pero no ten√©s ninguna<br />Empez√° a contestar preguntas para conseguirlas</>}
                </Typography> */}

                <div className={classes.progressContainer}>
                    <LinearProgress
                        className={classes.progress}
                        variant='determinate'
                        value={this.state.progress} />
                </div>
                <KeyCounter
                    className={this.props.classes.keyCounter}
                    keys={this.props.keys}
                    totalKeys={this.props.totalKeys}
                    onClick={() => this.setState({ showChest: true })} />
            </div>
        )
    }
}

export default withStyles(styles)(Chest);
