import './App.css';
import React, { Component } from 'react';
import { MuiThemeProvider, createMuiTheme, CssBaseline } from '@material-ui/core';
import { indigo, blueGrey, lightGreen } from '@material-ui/core/colors';
import { Switch, Route } from 'react-router'
import { HashRouter } from 'react-router-dom'
import Intro from './components/Intro';
import QuestionsContainer from './components/QuestionsContainer';
import AnswersContainer from './components/AnswersContainer';

const theme = createMuiTheme({
    palette: {
        type: 'dark',
        primary: { main: lightGreen[300] },
        secondary: { main: indigo.A100 },
        background: { default: blueGrey[900] },
    },
    typography: {
        useNextVariants: true,
        allVariants: { color: 'white' }
    }
});

export default class App extends Component {
    render() {
        return (
            <MuiThemeProvider theme={theme}>
                <CssBaseline />
                <div className="App">
                    <HashRouter>
                        <Switch>
                            <Route exact path="/" component={Intro} />
                            <Route exact path="/questions" render={() => {
                                return <QuestionsContainer
                                    onCorrectAnswer={this.handleCorrectAnswer} />
                            }} />
                            <Route exact path="/answers" component={AnswersContainer} />
                        </Switch>
                    </HashRouter>
                </div>
            </MuiThemeProvider>
        );
    }
}
